* {
  margin: 0;
  padding: 0;
}

// *, *:before, *:after {
//   box-sizing: inherit;
// }


@import url('https://fonts.googleapis.com/css?family=Roboto');
@import url('https://fonts.googleapis.com/css?family=Baloo');

$google-blue: #0288D1;
$google-border: #D0D3D5;


body {
  background:  $google-border;

  a {
    text-decoration: none;
  }

  .mainHeader {
    width: 100%;
    height: 50px;
    background-color: white;
    // box-shadow: 10px 10px 5px #888888;
    z-index:98;

      .headerLogo {
        font-family: 'Baloo';
        font-size: 35px;
        text-decoration: underline;
        padding-top: 0px;
        padding-left: 15px;
        color: black;

      }

  }

  .sideMenu {
    margin-top: 50px;
    right: 0;
    position: fixed;
    z-index:99997;
    width: 250px;
    height: 400px;
    background-color: white;
    margin-bottom: 30px;
    visibility: visible;
    border: 1px solid $google-border;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;

      .menu-link-container {
        right: 0;
        position: absolute;
        height: 35vh;
        width: 80%;
        float: right;
        margin-top: 177px;
        margin-right: 12px;
        text-align: center;
        // border :1px solid black;

          .menu-link {
            right: 0;
            float: right;
            margin-bottom: 40px;
            position: relative;
            font-size: 25px;
            font-family: 'Roboto', sans-serif;
            word-spacing: 0px;
            letter-spacing: 0px;
            font-weight: bold;
            color: $google-blue;
            // background: $google-border;
            // border-bottom: 3px solid black;
            width: 100%;
          }

          .menu-link:hover {
            background-color: $google-border;
          }

      }
  }

  main {
    // position: absolute;
    width: 100%;
    height: 100vh;
    background: #ECEFF1;
    align-self: center;
    overflow: auto;
    margin: auto;
    padding-bottom: 15px;

    // carousel

    .carousel {
      width: 96%;
      height: 35vh;
      // display: flex;
      // align-content: center;
      margin-top: 15px;
      margin-bottom: 40px;
      margin-left: 2%;
      align-self: center;
      draggable: true;
      autoPlay: true;
      prevNextButtons: false;

      #carousel-cell {
        // position: absolute;
        width: 100%; /* half-width */
        height: 100%;
        margin-right: 10px;
        // transform: translate(2%, 0) !important;
        border: 1px solid $google-border;

        .carousel-img {
          // position: relative;
          width: 100%;
          height: 100%;

            }
        }
    }

        // home view

        .text-container {
          width: 95%;
          height: 35vh;
          margin: auto;
          margin-bottom: 35px;
          background: white;
          border: 1px solid $google-border;
          text-align: center;

            .title-container {
              height: 50px;
              width: 100%;
              background: $google-blue;
              display: flex;
              justify-content: space-around;
              align-items: center;
              box-shadow: 0 4px 6px -6px #222;

                .home-title {
                  font-family: 'Roboto';
                  font-weight: 600;
                }

            }

            .text-seperator {
              height: 10px;
              width: 65%;
              border-bottom: 2px solid $google-border;
              margin: auto;
              margin-bottom: 10px;
            }

            #home-text {
              font-family: 'Roboto';
              width: 80%;
              margin-left: 10%;
              padding-bottom: 60px;

            }
          }

          // fix view

          .product-container {
            width: 96%;
            height: 800px;
            margin: auto;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;

              .product {
                width: 48%;
                height: 42vh;
                margin-bottom: 35px;
                border: 1px solid $google-border;
                background: white;
                // align-items: center;

                  .phone-title-container {
                    height: 30px;
                    width: 100%;
                    background: $google-blue;
                    display: flex;
                    justify-content: space-around;
                    align-items: center;

                      .phone-title {
                        font-family: 'Roboto';
                        font-weight: 600;
                      }
                  }

                  .text-seperator {
                    height: 10px;
                    width: 65%;
                    border-bottom: 2px solid $google-border;
                    margin: auto;
                    margin-bottom: 10px;

                  }

                  .phone-img-container {
                    width: 80%;
                    margin: auto;
                    height: 25vh;
                    overflow: hidden;
                    margin-bottom: 8px;
                    // border: 1px solid black;
                    // display: flex;
                    // flex-wrap: wrap;
                    // justify-content: space-around;
                    // align-content: center;

                      .phone-image {
                        margin-left: 7.5%;
                        // border: 1px solid black;
                        width: 85%;
                        // height: 100%;
                        // height: 100%;
                        // margin: auto;
                      }
                  }

                  .pricing-container {
                    width: 100%;
                    margin-left: 5px;

                      .pricing-text {
                        font-family: 'Roboto';

                      }
                  }
              }
          }

          .about-section {
            width: 96%;
            margin: auto;
            background-color: white;
            border: $google-border;
            font-family: 'Roboto';
            text-align: center;
            align-items: center;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;


            .about-title-container {
              height: 50px;
              width: 100%;
              background: $google-blue;
              display: flex;
              justify-content: space-around;
              align-items: center;
              margin-bottom: 10px;
            }

            .about-text {
              font-family: 'Roboto';
              margin-bottom: 20px;
              width: 80%;
              // margin: auto;
            }
          }


        }

        // menu button

        .menuButton {
          position: fixed;
          z-index:99999;
          height: 40px;
          width: 40px;
          border: none;
          border-radius: 50%;
          background: $google-border;
          bottom: 40px;
          right: 40px;
          animation: button-change 10s infinite;
        }

      }

      @media (min-width: 550px) {

        .main {
          width: 70%;
        }


      }

      @media (min-width: 650px) {

        .main {
          width: 60%;
        }

      }

      @media (min-width: 750px) {

        .main {
          width: 50%;
        }

        .secondary {
          height:206px !important;
        }

      }

      @media (min-width: 850px) {

        .main {
          width: 40%;
        }

        .sideMenu {
          margin-top: -130px !important;
          right: 0 !important;
          border: none !important;
        }

        .menuButton {
          top: 5px;
          right: 5px;
        }

        .carousel {
          // margin-top: 0px !important;
        }

      }


      @media (min-width: 1000px) {
        .phone-img-container {
          height: 28vh !important;
        }

        .phone-image {
          width: 70% !important;
          margin-left: 15% !important;
        }
      }

      @media (min-width: 1550px) {
        .phone-img-container {
          height: 32vh !important;
        }

        .phone-image {
          width: 70% !important;
          margin-left: 15% !important;
        }
      }

      @keyframes button-change{
        0% {background: $google-border}
        50% {background: $google-blue}
        100% {background: $google-border}
      }
